.nav {
  --bs-nav-link-color: rgb(255, 255, 255);
  --bs-nav-link-hover-color: rgb(156, 163, 175);
  --bs-nav-link-disabled-color: #6c757d;
}

.navbar {
  --bs-navbar-color: rgb(156, 163, 175);
  --bs-navbar-hover-color: #d9d9d9;
  --bs-navbar-disabled-color: #a6a6a6;
  --bs-navbar-active-color: #bfbfbf;
  --bs-navbar-brand-color: rgb(255, 255, 255);
  --bs-navbar-brand-hover-color: #d9d9d9;
  --bs-navbar-toggler-icon-bg: url("data:image/svg+xml,%3csvg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 30 30'%3e%3cpath  stroke='rgba%28255, 255, 255, 0.85%29' stroke-linecap='round' stroke-miterlimit='10' stroke-width='2' d='M4 7h22M4 15h22M4 23h22'/%3e%3c/svg%3e");
}

.nav-tabs {
  --bs-nav-tabs-border-color: rgba(229, 231, 235, 0.164);
  --bs-nav-tabs-link-hover-border-color: rgba(229, 231, 235, 0.274);
  --bs-nav-tabs-link-active-color: rgb(255, 255, 255);
  --bs-nav-tabs-link-active-bg: linear-gradient(to bottom, rgb(35, 45, 59), rgb(17, 24, 39));
  --bs-nav-tabs-link-active-border-color: rgba(229, 231, 235, 0.274);
}

.nav-link {
  color: rgb(156, 163, 175);
}
.nav-link:hover {
  color: rgb(255, 255, 255);
}
.nav-link:active {
  color: #cccccc;
}